import Vue from "vue";
import Vuex from "@zkty-team/vuex";
Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    count: 0,
  },
  mutations: { // 同步
    jia(state) {
      state.count++;
    },
    jian(state) {
      state.count--;
    },
  },
  actions: {}, // 异步
});

export default store;
